  openStartFilter() {
    // const modal = this.modalCtrl.create('StartStartFilterPageFilter');
    // modal.present();
    let modal = this.modalCtrl.create("StartFilterPage", {
      html: true,
      closeable: false,
      animated: true,
      showBackdrop: true,
      backdropDismiss: false,
    });
    modal.present();
    modal.onDidDismiss((data) => {
      console.log(data);
      if (data) {
        this.stepsToBeRemoved.forEach((item: string) => {
          this.data = this.data.filter((obj: Node) => {
            return obj.title !== item;
          });
        });
      } else {
        this.data = this.data.filter((obj: Node) => {
          return obj.title !== "3.4 Diagnostik";
        });
      }
      this.onSelectionChange();
    });
  }

